}

.PopUpContent::after{
	content: "";
	position: absolute;
	left: -3.2vw;
	bottom: 3vh;
	margin-left: -0.46vw;
	width: 0;
    height: 0;
    border-left: 2vw solid transparent;
    border-right: 2vw solid transparent;
    border-top: 3vw solid #0F1259;
    transform: rotate(90deg);
}
.PopUpContent::before{
	content: "";
	position: absolute;
	left: -2.5vw;
	bottom: 3vh;
	margin-left: -0.46vw;
    border-left: 2vw solid transparent;
    border-right: 2vw solid transparent;
    border-top: 3vw solid white;
    z-index: 10000;
    transform: rotate(90deg);
}

