
#BackButton{
	position:fixed;
	bottom: 2vw;
	right:40%;
	direction: rtl;
	display: flex;
	padding-right: 0.9vw;
}
#FrontButton{
	position:fixed;
	bottom: 2vw;
	left: 33%;
	direction: rtl;
	display: flex;
	padding-right: 1.2vw;
}


.FrontBackButtons{
	background-color: #FAA159;
	color:#0F1259;
	font-size: 1.7vw;
	border-radius: 30vw;
	border:none;
	cursor: pointer;
	width: 10vw;
	height: 4.2vw;
	transition: 0.3s;
	align-items: center;
	font-weight: 600;
	display: flex;
	justify-content: center;
}

.FrontBackButtons:hover{
	background-color:#F6841F;
	transition: 0.2s;
}
.leftarrowfont{
	transform: scaleY(1.2) scaleX(0.8) rotate(180deg);
	/*display: inline-block;*/
	margin-top: 0.7vh;
	margin-right: 1.5vw;
	font-size: 2.1vw;
}

.rightarrowfont{
	/*display: inline-block;
*/	padding-bottom: 0.4vh;
	margin-left: 0.4vw;
	transform: scaleY(1.2) scaleX(0.8);
	font-size: 2.1vw; 
}
.FrontBackButtonsDisabled{
	opacity: 0.3;
}

.FrontBackButtonsDisabled:hover{
	background-color: #FAA159;
	cursor: default;
}